    <div class="right">
      <p class="songName">{{ item.name }} <img src="../../img/bofang.png" alt=""></p>
      <p class="arName"><span class="icon3"> </span> {{ item.ar[0].name }}--{{item.al.name}}</p>
    </div>
  </div>
</template>

<script>
export default {
  props: ["item", "index"],
  methods: {},
};
</script>

<style scoped lang="less">
.hotSongList {
  width: 90%;
  margin: auto;
  height: 50px;
  border-bottom:1px rgba(0, 0, 0, 0.1) solid;
