<template>
  <div class="hot">
    <HotSongList
    :hotSongs="hotSongs"
    @update:music="$emit('update:music', $event)"
    @update:playlist="$emit('update:playlist', $event)"
    @play-type="$emit('play-type',$event)"
    :isloading2="isloading2"
    ></HotSongList>
  </div>
</template>

<script>
import HotSongList from "@/components/HotSongList.vue";
export default {
  components: { HotSongList },
  data: function () {
    return {
      hotSongs: [],
      hotSongId: [],
      isloading2:true,
    };
  },
  created() {
    this.axios.get("/top/list?idx=1").then((response) => {
      // console.log(response.data.playlist.trackIds);

      for (var k in response.data.playlist.trackIds) {
        this.hotSongId.push(response.data.playlist.trackIds[k].id);
      }
      this.hotSongId = JSON.parse(JSON.stringify(this.hotSongId));


      for(var i=0;i<30;i++){
        this.axios.get("/song/detail?ids=" +this.hotSongId[i]+ "").then((response) => {
        
          this.hotSongs.push(response.data.songs[0]);
          // console.log(this.hotSongs)
        }).finally(()=>{
          this.isloading2=false;
        });
    }
    });
    
    
  },
  methods: {
    sendMsg(){
      this.$emit=("getMsg","我是儿子的数据")
    }
  },
};
</script>

